import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'
import format from 'date-fns/format'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

import AppImageLoader from '../Common/AppImageLoader/AppImageLoader'

const styles = theme => ({
  root: {
    margin: '.5rem'
  },
  loader: {
    margin: '5rem',
    textAlign: 'center'
  },
  avator: {
    position: 'relative',
    height: '30vh',
    maxWidth: '100vw',
    overflow: 'hidden',
    width: '100%'
  }
})

class RegistersContent extends Component {
  render () {
    const {
      classes,
      registerList,
      isDataLoaded,
      deleteRegisterItem,
      setDataLoaded
    } = this.props

    return (
      <div>
        <Grid container justify='center'>
          {
            isDataLoaded
              ? (
                <Grid item xs={12} container>
                  {
                    registerList.map(register => {
                      const {key, avator, name, timestamp} = register

                      return (
                        <Grid item xs={12} sm={6} key={key}>
                          <Card className={classes.root}>
                            <div className={classes.avator}>
                              <AppImageLoader
                                src={avator}
                                alt='avator'
                                backgroundSize='cover'
                                loaderSize={60}
                                title={name} />
                            </div>
                            <CardContent>
                              <Typography gutterBottom variant='caption'>
                                Registered
                              </Typography>
                              <Typography gutterBottom variant='body2'>
                                {format(new Date(timestamp), 'YYYY/MM/DD HH:mm:ss')}
                              </Typography>
                            </CardContent>
                            <CardActions>
                              <Button
                                size='small'
                                color='primary'
                                component={Link}
                                to={`/user/${key}`}>
                                See User
                              </Button>
                              <Button
                                size='small'
                                style={{color: 'red', marginLeft: 'auto'}}
                                onClick={() => {
                                  deleteRegisterItem(register)
                                  setDataLoaded(false)
                                }}>
                                Delete
                              </Button>
                            </CardActions>
                          </Card>
                        </Grid>
                      )
                    })
                  }
                </Grid>
              )
              : (
                <Grid item xs={12}>
                  <div className={classes.loader}>
                    <CircularProgress size={80} />
                  </div>
                </Grid>
              )
          }
        </Grid>
      </div>
    )
  }
}

export default withStyles(styles)(RegistersContent)



