import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import RegistersContent from '../component/Registers/RegistersContent'

class Registers extends Component {
  componentDidMount () {
    const {setCurrentRoute, setDataLoaded, getRegisterList} = this.props

    setCurrentRoute('/registers')
    setDataLoaded(false)
    getRegisterList()
  }

  render () {
    return (
      <main>
        <RegistersContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    registerList: state.action.registerList,
    isDataLoaded: state.action.isDataLoaded
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getRegisterList: () => dispatch(actionCreators.getRegisterList()),
    deleteRegisterItem: (register) => dispatch(actionCreators.deleteRegisterItem(register)),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    setCurrentRoute: (currentRoute) => dispatch(actionCreators.setCurrentRoute(currentRoute))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Registers)



